  border-width: 0 0px;
  display: inline-block;
  padding: 0px 20px;
  margin: 20px;
  transition: 0.1s cubic-bezier(0.03, 0.4, 0.67, 0.69);
  position: relative;
}

.brackets-red:before {
  content: " ";
  display: block;
  width: 3px;
  position: absolute;
  height: 100%;
  left: 5px;
  top: 0;
  background: #e10b31;
  transition: 0.1s cubic-bezier(0.03, 0.4, 0.67, 0.69);
}

.brackets-red:after {
  content: " ";
  display: block;
  width: 3px;
  position: absolute;
  height: 100%;
  right: 5px;
  top: 0;
  background: #e10b31;
  transition: 0.1s cubic-bezier(0.03, 0.4, 0.67, 0.69);
}

.brackets-red:hover {
  background-position: top 0 left 1px, top 0 right 1px, bottom 0 left 1px, bottom 0 right 1px;
  left: 0;
}

.brackets-red:hover:before {
  left: -1px;
}

.brackets-red:hover:after {
  right: -1px;
}

