.scroll-top {
  opacity: 0;
  transition: opacity .4s ease;
  z-index: 2000;
  position: fixed;
  right: 20px;
  bottom: 20px;
  background: #111111;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  display: inline-block;
}

