  .scroll-top {
    display: none;
  }
}

.scroll-top.in {
  opacity: 1;
}

.scroll-top .scroll-arrow {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 24px;
  height: 3px;
  margin-top: 4px;
  background: #E10B31;
  border: 0;
  padding: 0;
  transition: all 0.2s ease;
}

.scroll-top .scroll-arrow:before {
  content: " ";
  margin-top: -14px;
  display: block;
  width: 0;
